import React, { Component } from 'react';

//Components
import Header from '../../common/Header';
import Footer from '../../common/Footer';
import styled from 'styled-components';
import SpecialistSelectionSection from '../../common/Sections/SpecialistSelectionSection';
import Section from '../../common/Sections/FaqSection/index';
import NavLink from '../../common/NavLink/index';
import arrow from '../../img/link-arrow.svg';
import { faqData } from '../../data/faq_data';

class FaqSectionPage extends Component {
    componentDidMount() {
        document.title = 'Служба помощи'
    }
    render() {
        return (
            <div>
                <Header />

                <Background>
                    <Content>
                        <Navigation>
                            <NavLink path={'/faq'} name={'Служба помощи'} />
                            <Arrow> <img src={arrow} alt='' /> </Arrow>
                            <NavLink path={'/faq/section'} name={'Уход за пациентами'} color={'#9b9b9b'} />
                        </Navigation>
                        <Section data={faqData[0]} />
                    </Content>
                </Background>

                <SpecialistSelectionSection />
                <Footer />
            </div>
        )
    }
}

// @media(max-width: 480px) {}

const Arrow = styled.div`
    height: 100%;
    margin: 0 2.5%;
    img {
        width: 8px;
    }
`
const Navigation = styled.div`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding-top: 10px;
    margin-bottom: 20px;
    @media(max-width: 768px) {
        margin-bottom: 10px;
    }
`
const Background = styled.div`
    background-color: #edebe3;
    width: 100%;
    @media(max-width: 768px) {
        padding-top: 60px;
    }
`
const Content = styled.div`
    padding-left: 4%;
    padding-right: 4%;
    max-width: 1200px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0 auto;
`

export default FaqSectionPage;





